import { Box, Typography, styled} from '@mui/material';
import { useContext, useEffect, useState } from 'react';
import { formatDate } from '../../../utils/common-utils';
import { AccountContext } from '../../../context/AccountProvider';
import { setConversation, getConversation } from '../../../service/api';

const Component = styled(Box)`
    display: flex;
    height: 45px;
    padding: 13px 0;
    cursor: pointer;
    overflow: hidden;
`

const Image = styled('img')({
    width: 50,
    height: 50,
    objectFit: 'cover',
    borderRadius: '50%',
    padding: '0 14px'

})

const Container = styled(Box)`
   display: flex;
   max-width: 100%;
`

const TimeStyle = styled(Typography)`
   margin-left: auto;
   font-size: 12px;
   color: #0000099;
   margin-right: 20px;
` 

const Text = styled(Typography)`
     display: block;
     color: rgba(0, 0, 0, 0.6);
     font-size: 14px;
     overflow: hidden;
     max-width: 300px;
`

const Conversation = ({user}) => {

    const { setPerson, account, newMessageFlag, setNewMessageFlag,deleteMessageFlag } = useContext(AccountContext);

    const [message, setMessage] = useState({});

  useEffect(() => {
     const getConversationDetails = async () => {
        const data = await getConversation({senderId: account.sub, receiverId: user.sub});
        setMessage({text: data.message, timestamp: data.updatedAt});
    }
     getConversationDetails();
    },[newMessageFlag, deleteMessageFlag])

    const getUser = async () => {
        setPerson(user);
        await setConversation({senderId: account.sub, receiverId: user.sub});
    }

    return(
        
        <Component onClick={() => getUser()}>
            <Box>
                <Image src={user.picture} alt="dp"/>
            </Box>
            <Box style={{width : '100%'}}>
                <Container>
                   <Typography>{user.name}</Typography>
                    {
                        message?.text && 
                        <TimeStyle>{formatDate(message?.timestamp)}</TimeStyle>
                    }
                </Container>
                <Box>
                   <Text>{message?.text?.includes('localhost')?'media' : message.text}</Text>
                </Box>
            </Box>
        </Component>
        
    )
}

export default Conversation;